<template>
  <el-card class="form-container" shadow="never">
    <el-form :model="productCate"
             :rules="rules"
             ref="productCateFrom"
             label-width="150px">
      <el-form-item label="分类名称：" prop="name">
        <el-input v-model="productCate.name"/>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" @click="commit('productCateFrom')">提交</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
  import {getProductCate, updateProductCate,createProductCate} from '@/api/productCate';

  const defaultProductCate = {
    id: '',
    name: '',
  };
  export default {
    name: "ProductCateDetail",
    props: {
      isEdit: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        productCate: Object.assign({}, defaultProductCate),
        rules: {
          name: [
            {required: true, message: '请输入分类名称', trigger: 'blur'},
            {min: 1, max: 140, message: '长度在 2 到 140 个字符', trigger: 'blur'}
          ]
        },
      }
    },
    created() {
      if (this.isEdit) {
        getProductCate(this.$route.query.id).then(response => {
          this.productCate = response.data;
        });
      } else {
        this.productCate = Object.assign({}, defaultProductCate);
      }
    },
    methods: {
      commit(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if(this.isEdit){
              updateProductCate(this.productCate).then(response=>{
                this.$message({
                  type: 'success',
                  message: '提交成功',
                  duration:1000
                });
                this.$router.push({path:'/pms/productCate'});
              });
            }else{
              createProductCate(this.productCate.name).then(response=>{
                this.$message({
                  type: 'success',
                  message: '提交成功',
                  duration:1000
                });
                this.$router.push({path:'/pms/productCate'});
              });
            }
          } else {
            this.$message({
              message: '验证失败',
              type: 'error',
              duration:1000
            });
            return false;
          }
        });
      }
    },
  }
</script>

<style scoped>

</style>



